<template>
  <div >
    <mu-appbar >
      <mu-text-field  class="appbar-search-field" v-model="value" slot="left" hintText="请输入搜索内容"/>
      <mu-flat-button color="white" label="搜索" slot="right" @click="pressSearch(true)"/>
    </mu-appbar>
    <div class="demo-infinite-container">
      <mu-list>
        <template v-for="(item,index) in songList">
          <div class="item-container" @click="play(index)">
            <div class="div-sonename" v-html="item.SongName"></div>
            <div class="div-singername" v-html="item.SingerName"></div>
          </div>
          <mu-divider/>
        </template>
      </mu-list>
      <mu-infinite-scroll :loading="loading" @load="loadMore"/>
    </div>
    <div class="player-container">
      <aplayer v-if="playConfig"
               :autoplay="autoplay"
               :music="playConfig"
               :showLrc="showLrc"
               :float="float"
      />
    </div>

  </div>
</template>

<script>
  import Aplayer from 'vue-aplayer'
  export default {
    name:'musicplayer',
    components:{
      Aplayer
    },
    data () {
      return {
        value: '',
        response:"",
        songList:[],
        loading:false,
        page:1,
        playReponse:{},
        showLrc:true,
        playConfig:null,
        autoplay:true,
        float:true
      }
    },
    mounted:()=>{
    },
    methods:{
      resetPageIndex:function () {
        this.page = 1;
      },
      pressSearch:function(isNeedReset){
        if(isNeedReset) {
          this.resetPageIndex()
        }
        let requestBaseUrl = 'http://songsearch.kugou.com/song_search_v2?filter=2&iscorrection=1&privilege_filter=0&_=1489023388641&keyword='+this.value + '&page='+this.page + '&pagesize='+'30&userid=-1&clientver=&platform=WebFilter&tag=em';
        var requestParam = {}
        this.$http.jsonp(requestBaseUrl,requestParam).then(response => {
          this.loading = false;
          this.response = response.body
          this.songList = this.response.data.lists
        }, response => {
          this.loading = false;
        });
      },
      loadMore:function () {
        console.log('123kaishiloadmore')
        this.loading = true
        this.page++
        this.pressSearch(false)
      },
      play:function (index) {
        let musicHash = this.songList[index].FileHash
        let requestMusicUrl = 'http://www.kugou.com/yy/index.php?r=play/getdata&hash=' + musicHash
        this.$http.jsonp(requestMusicUrl).then(response => {
          let resposne = response.body.data
          this.playConfig = {
            author:resposne.author_name,
            title:resposne.song_name,
            url:resposne.play_url,
            pic:resposne.img,
            lrc:resposne.lyrics
          }
        }, response => {
        });
      }
    }
  }
</script>

<style lang="less">
  .appbar-search-field{
    color: #FFF;
    margin-bottom: 0;
  &.focus-state {
     color: #FFF;
   }
  .mu-text-field-hint {
    color: fade(#FFF, 54%);
  }
  .mu-text-field-input {
    color: #FFF;
  }
  .mu-text-field-focus-line {
    background-color: #FFF;
  }
  }

  .demo-infinite-container{
    height: 300px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }

  .item-container {
    height: 30px;
    display: flex;
    justify-content: space-between;
  }

  .div-sonename {
    text-overflow : ellipsis;
    white-space:nowrap;
    overflow:hidden;
    width:60%;
    text-align: left;
    margin: 2px;
  }

  .div-singername{
    text-overflow : ellipsis;
    white-space:nowrap;
    overflow:hidden;
    width:40%;
    text-align: right;
    margin: 2px;
    margin-right: 5px;
  }

  .player-container {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }

</style>



